.d7 {
  background: #535fc7;
}

.d7:after {
  content: "";
  clear: both;
  display: table;
}

.d7 form {
  width: auto;
  float: right;
  margin-right: 30px;
}

.d7 input {
  width: 250px;
  height: 42px;
  background: #fff;
  border: 2px solid #3d324e;
  border-radius: 42px;
  outline: none;
  padding-left: 15px;
  transition: all .3s linear;
  position: relative;
}

.d7 input:focus {
  width: 300px;
}

.d7 button {
  width: 42px;
  height: 42px;
  background: none;
  border: none;
  position: absolute;
  top: -2px;
  right: 0;
}

