* {
  box-sizing: border-box;
}
body {
  margin: 0;
}
div {
  padding: 10px 0;
  display: flex;
}
form {
  position: relative;
  width: 300px;
  margin: 0 auto;
}
img {
  width: 100%;
}

.d7 {
  background: #535fc7;
  
}
.d7:after {
  content: '';
  clear: both;
  display: table;
}
.d7 form {
  width: auto;
  float: right;
  margin-right: 30px;
}
.d7 input {
  width: 250px;
  height: 42px;
  padding-left: 15px;
  border-radius: 42px;
  border: 2px solid #3d324e;
  background: #ffffff;
  outline: none;
  position: relative;
  transition: 0.3s linear;
}
.d7 input:focus {
  width: 300px;
}
.d7 button {
  width: 42px;
  height: 42px;
  background: none;
  border: none;
  position: absolute;
  top: -2px;
  right: 0;
}
.d7 button:before {
  content: "\f105";
  font-family: FontAwesome;
  color: #324b4e;
}
.gallery{
  flex-wrap: wrap;
  gap: 20px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.photo-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 23%;
}